<template>
  <div id="app">
    <QnaHeader></QnaHeader>
    <router-view></router-view>
  </div>
</template>

<script>
import QnaList from "./components/QnaList.vue";
import QnaHeader from "./components/QnaHeader.vue";
import QnaInput from "./components/QnaInput.vue";
import VueRouter from "vue-router";
import QnaUpdate from "./components/QnaUpdate.vue";
import QnaDetail from "./components/QnaDetail.vue";
import Vue from "vue";

Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  routes: [
    { path: "/", component: QnaList },
    { path: "/qna/", component: QnaList },
    { path: "/qna/list", component: QnaList },
    { path: "/main.jsp", component: QnaList },
    { path: "/qna/input", component: QnaInput },
    { path: "/qna/update/:no", component: QnaUpdate },
    { path: "/qna/detail/:no", component: QnaDetail }
  ]
});

export default {
  name: "app",
  components: {
    QnaHeader
  },
  router
};
</script>

